body {
  background-image: url("./img/lake-in-autumn.webp");
  background-repeat: no-repeat;
  background-color: #111;
  background-position-x: center;
  background-size: cover;
  height: 100%;
  margin: 0px;
  min-width: 512px;
}

.App {
  margin-inline: 12px;
}

.Menu {
  margin: auto;
  margin-block: 1vh;
  min-width: 512px;
  max-width: 1280px;
  height: 90vh;
  background-color: rgba(100, 100, 100, 0.5);
  border: 0.2vh solid rgba(255, 255, 255, 0.8);
  padding: 0.3vh;
  display: flex;
}

.Left{
  border: 1px solid rgba(100, 100, 0, 0.5);
  border-radius: 8px;
  background-color: rgba(120, 120, 0, 0.3);
  width: 65%;
  margin: 4px;
  display: flex;
  flex-direction: column;
}

.Left > h1 {
  margin: auto;
  width: 98%;
  text-align: center;
  background: linear-gradient(0.25turn, rgba(187, 153, 42, 0.1),
    rgba(180, 152, 28, 0.9), rgba(187, 153, 42, 0.1));
}

.Right{
  border: 1px solid rgba(85, 0, 120, 0.5);
  border-radius: 8px;
  background-color: rgba(85, 0, 120, 0.3);
  width: 35%;
  margin: 4px;
}


.FileUpload {
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  background-color: rgba(65, 65, 65, 0.5);
  margin: 4px;
}

.FileForm {
  width: 90%;
  height: 21px;
  padding: 1px 2px;
  border: 1px solid black;
  background-color: white;
  margin: auto;
  margin-block: 4px;
}

.NameForm {
  width: 90%;
  height: 21px;
  padding: 1px 2px;
  border: 1px solid black;
  background-color: white;
  margin: auto;
  margin-block: 4px;
}

.WeightForm {
  width: 90%;
  height: 21px;
  padding: 1px 2px;
  border: 1px solid black;
  background-color: white;
  margin: auto;
  margin-block: 4px;
}

.Upload {
  width: 4rem;
  height: 2rem;
  margin-left: auto;
}

.Explorer {
  border: 2px solid black;
  background-color: rgba(65, 65, 65, 0.5);
  margin: 4px;
  padding: 4px;
}

.Refresh {
  width: 100%;
}

.Empty {
  text-align: center;
  font-size: 2rem;
}

.Directory {
  padding-left: 20px;
  overflow: hidden;
  white-space: nowrap;
}

.FileLink {
  display: list-item;
  margin: 4px;
}

.FileLink:link {color: #DDD;} .FileLink:visited {color: #111;}
.FileLink:hover {color: #005;} .FileLink:active {color: #191;}

.Bottom {
  margin-inline: -12px;
  background-color: #333;
  height: 7vh;
  min-width: 536px;
}